import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex)

// 应用初始状态
const state = {
    login: 0
}

// 定义所需的 mutations
const mutations = {
    USER_LOGIN(state) {
        state.login=1;
    },
    USER_LOGOUT(state) {
        state.login=0;
    }
}

// 创建 store 实例
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


